@import url('https://fonts.googleapis.com/css2?family=Lato&family=Outfit:wght@300;400;500;600;700&display=swap');

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-weight: 'Outfit', sans-serif;
}

body{
  background: #E8c5bC;
}

h1,
h4,
p,
h2,
li,
a{
  color: #fff;
  text-decoration: none;
}

ul{
  list-style-type: none;
}

.btn{
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background: transparent;
  color: #fff;
  border: 1px solid #fff;
  font-weight: 600;
  cursor: pointer;

}

.btn-light {
  background: transparent;
  color: #fff;
}

.btn:hover{
  background: rgba(225, 225, 225, 0.2);
  color: #fff;
  transition: 0.3s;
}