.hero-img{
    width: 100%;
    background: rgba(0, 0, 0, 0.2);
    height: 60vh;
    position: relative;
}

.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero-img h1{
    font-size: 2.4rem;
}

.hero-img p{
    font-size: 1.4rem;
    text-align: center;
}
.hero-img::before{
    content: "";
    background-image: url("https://images.unsplash.com/photo-1555231964-2efe395ef6a7?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MTUyfHxwaW5rJTIwYWVzdGhldGljJTIwdGVjaHxlbnwwfHwwfHw%3D&auto=format&fit=crop&w=1600&q=60");
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

@media screen and (max-width: 640px){
    .hero-img h1{
        font-size: 2rem;
    }
}